}

.hvemErDuLarer {
  background: #2696bf url("../img/icons/hveemErDuIcons_03.png") no-repeat;
  background-size: 150px 150px;
  box-shadow: 0 2px 3px #2696bf, 0 0 0 3px #2688b1;
  transition: .2s;
}

.image-is-circle {
  border-radius: 100%;
  width: 150px;
  height: 150px;
}

.komiGang_01 {
  background: #2696bf url("../img/icons/komiGang_01.png") no-repeat;
  background-size: 150px 150px;
  box-shadow: 0 2px 3px #2696bf, 0 0 0 3px #2688b1;
  transition: .2s;
}

.komiGang_02_student {
  background: #2696bf url("../img/icons/komiGang_02_student.png") no-repeat;
  background-size: 150px 150px;
  box-shadow: 0 2px 3px #2696bf, 0 0 0 3px #2688b1;
  transition: .2s;
}

.komiGang_02_bedrift {
  background: #2696bf url("../img/icons/hveemErDuIcons_01.png") no-repeat;
  background-size: 150px 150px;
  box-shadow: 0 2px 3px #2696bf, 0 0 0 3px #2688b1;
  transition: .2s;
