/* Footer */
.bidragsytere, .samarbeidspartnere {
    text-decoration: none;
    border-bottom: 0 !important;
    margin: 0 15px 15px 0;
}

.bidragsytere img, .samarbeidspartnere img{
    width: 120px;
    -webkit-filter: grayscale(100%);
    filter: grayscale(10);
    transition: .2s;
}

.bidragsytere img:hover, .samarbeidspartnere img:hover {
    -webkit-filter: grayscale(0%);
    filter: grayscale(0);
    transition: .2s;
}


